[class*=taroify-hairline]::after {
  position: absolute;
  box-sizing: border-box;
  pointer-events: none;
  content: " ";
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid var(--gray-3, #ebedf0);
  transform: scale(0.5);
}

.taroify-hairline, .taroify-hairline--top, .taroify-hairline--left, .taroify-hairline--right, .taroify-hairline--bottom, .taroify-hairline--surround, .taroify-hairline--top-bottom {
  position: relative;
}
.taroify-hairline--top::after {
  border-top-width: 2px;
}
.taroify-hairline--left::after {
  border-left-width: 2px;
}
.taroify-hairline--right::after {
  border-right-width: 2px;
}
.taroify-hairline--bottom::after {
  border-bottom-width: 2px;
}
.taroify-hairline--top-bottom::after, .taroify-hairline-unset--top-bottom::after {
  border-width: 2px 0;
}
.taroify-hairline--surround::after {
  border-width: 2px;
}

.style-demo .animate-block {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 200px;
  height: 200px;
  margin: -100px 0 0 -100px;
  background-color: #1989fa;
  border-radius: 16px;
}
.style-demo .ellipsis-text {
  margin-bottom: 32px;
  font-size: 28px;
  line-height: 36px;
  color: #323233;
}
.style-demo .ellipsis-text:last-child {
  margin-bottom: 0;
}
.style-demo .hairline-line {
  height: 60px;
  background-color: #fff;
}
.style-demo .hairline-line::after {
  top: 10px;
}